import React from "react";
import { CSSTransition } from "react-transition-group";

const DownloadForm = (props) => {
    return (
        <CSSTransition
            in={props.isOpen}
            timeout={200}
            classNames="popup"
            unmountOnExit
        >
            <div className="popup-download" ref={props.ref}>
                <div className="popup-download__body">
                    <div className="modal-close" onClick={() => { props.setIsOpen(false)}}></div>
                    <div className="error"></div>
                    <div className="content">
                        <div className="content__title">–°—Å—ã–ª–∫–∞ –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ —Ä–∞–±–æ—Ç–∞–µ—Ç ...</div>
                        <div className="content__text">–ë—É–¥—å—Ç–µ –ø–µ—Ä–≤—ã–º–∏ –∫—Ç–æ —É–∑–Ω–∞–µ—Ç –æ –≥–æ—Ç–æ–≤–Ω–æ—Å—Ç–∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è - –æ—Å—Ç–∞–≤–∏–≤ —Å–≤–æ—é –ø–æ—á—Ç—É –Ω–∏–∂–µ!</div>
                        <input className="input" placeholder="–í–∞—à–∞ –ø–æ—á—Ç–∞" type="email"></input>
                        <div className="btn">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</div>
                    </div>
                </div>
            </div>
        </CSSTransition>
    );
}

export default DownloadForm;